@font-face {
  font-family: "Modak";
  font-weight: 400;
  font-style: normal;
  src: url("Modak.woff2") format("woff2"), url("Modak.ttf") format("truetype"),
    url("Modak.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  src: url("Montserrat-Regular.woff2") format("woff2"),
    url("Montserrat-Regular.ttf") format("truetype"),
    url("Montserrat-Regular.woff") format("woff");
}
